.App {
  text-align: center;
  background-image: linear-gradient(#28d415, #0a802f);
  color: rgb(0,0,0);
  text-size-adjust: auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.hello {
  min-height: 100vh; /* height of the browser viewport */
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.headshot {
  width: 50%;
  height: 50%;
  border-radius: 10%;
}

.techskills {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.languages {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}

.language {
  height: 10%;
  width: 10%;
  opacity: 0.7;
  transition: 0.3s;
}
.language:hover {opacity: 1}


.navbar-custom {
  background-color: crimson;
}



.contactAndIntro {
  display : flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.gitHub {
  height: 20%;
  width: 20%;
  opacity: 0.7;
  transition: 0.3s;
}
.gitHub:hover {opacity: 1}