:local(.container) {
  position: relative;
  width: 50%;
}

:local(.projects) {
  min-height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

:local(.fundiesProjects) {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}

:local(.project) {
  display: block;
  width: 100%;
}

:local(.overlay) {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background-color: #a3abad;
}

:local(.text) {
  color: rgb(3, 3, 3);
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

:local(.container:hover .overlay) {
  opacity: 0.7;
}
